#left-sidebar {
    background-color: #001529;
    width: 180px;
    position: fixed;
    top : 30px;

    .clock-time{
        color: #d9d9d9;
        padding-left: 16px;
        padding-top: 10px;
        font-size: 12px;
        position: absolute;
        z-index: 1;

        button{
            margin-left: 10px;
        }
    }
    .ant-menu.ant-menu-root{
        padding-top: 35px;
    }
    .main-menu{
        list-style: none;
        padding-top: 10px; 
        padding-left: 0;       
        height: 100vh;
        margin-bottom: 0;
        a:visited, a:link {
            text-decoration: none;
        }
        li{
            position: relative;
            padding: 14px 10px 14px 16px;
            color: #f5f5f5;
            span{
                padding-left: 10px;
            }
            span, i{
                font-size: 12.5px;
                color: #f5f5f5;
            }
            i{
                font-size: 18px;
            }
        }
        a.active li{
            background: #000;
            color: #fff;
        }
        a:hover li{
            background: #555;
            color: #fff;
        }
    }
}