body{
    margin : 0;
}
.login{
    background-color: #555;
    min-height: 100vh;
    height: 100%;
    width: 100%;
    
    .login-warpper{
        max-width: 300px;
        margin: 0 auto;
        padding-top: 30px;
        padding-bottom: 25px;

        .login-form {
            padding: 20px 25px;
            background: #444444;
            border-radius: 5px;

            .ant-input-prefix{
                color: #d5d5d5;
            }
            input{
                background: #000;
                border-color: #525252;
                color: #f5f5f5;

                &::placeholder{
                    color: #616060 !important;
                }
            }
            input:-webkit-autofill,
            input:-webkit-autofill:hover, 
            input:-webkit-autofill:focus, 
            input:-webkit-autofill:active  {
                box-shadow: 0 0 0 30px white inset !important;
            }
            .has-error .ant-form-explain, .has-error .ant-form-split{
                font-size: 12px;
            }
        }
        .login-logo{        
            text-align: center;
            svg {
                height: 40px;
                margin-bottom: 5px;
            }
        }
        .login-form-button{
            width:100px;
            background: #000;
            border-color: #000;   
            color: #616060;

            &:hover{
                background: rgba(80, 80, 80, 0.85);
                border-color: #000;
                color: #000;  
            }            
        }
    }
    .version-log{
        margin-top: 10px;
        text-align: center;
    }
    .text-center {
        text-align: center;
    }
    .gray-fonts{
        color: #ddd;
    }
}