/* Global Layout */

.content-with-sidebar{
    margin-left: 180px;
    padding: 12px 15px;
}

// Over-ride ANT Design
.ant-table-thead >tr >th,
.ant-table-tbody >tr >td{
    padding: 8px 10px;
}
.ant-form-inline{
    .ant-form-item-control-wrapper{
        min-width: 200px;
        div{
            min-width: 200px;
        }
    }
    .ant-row{
        margin-bottom: 12px;        
    }
}


#content-root{
    header{
        position: fixed;
        width: 100%;
        background: #f2f2f2;
        z-index: 5;
        top: 0;
    }

    .content-with-sidebar{
        padding-top: 45px;
    }
}

.redeem-phone{
    .ant-form-explain{
        white-space: nowrap;
    }
}

.text-right{
    text-align: right;  
}